import { CartesianGrid, Legend, Line, LineChart, ResponsiveContainer, Tooltip, TooltipProps, XAxis, YAxis } from 'recharts';
import styled from 'styled-components';
import { stockList } from '../../../assets/data/stockList';
import { numToPercent } from '../../../utils/helper';

const filteredList = stockList.filter(el => el.preClose && el.open && el.high && el.low && el.close);
const list = filteredList.map((el, i) => {
    const { preClose, high, open, low } = el;
    const highPercent = numToPercent((Number(high) - Number(preClose)) / Number(preClose), 2);
    const openPercent = numToPercent((Number(open) - Number(preClose)) / Number(preClose), 2);
    const lowPercent = numToPercent((Number(low) - Number(preClose)) / Number(preClose), 2);
    return { date: 'ㅁㅁㅁ', preClose: 0, high: highPercent, open: openPercent, low: lowPercent };
});

const CustomTooltip = ({ active, payload }: TooltipProps<number | string | Array<number | string>, number | string>) => {
    if (active && payload && payload.length) {
        return (
            <div style={{ background: 'rgba(255, 255, 255, 0.7)', padding: '0.5rem' }}>
                <p style={{ color: 'red' }}>{`${payload[0].dataKey} : ${payload[0].value}`}</p>
                <p style={{ color: 'blue' }}>{`${payload[2].dataKey} : ${payload[2].value}`}</p>
            </div>
        );
    }
    return null;
};

const Outter = styled.div`
    overflow: scroll;
    height: 400px;
    position: relative;
`;

export default function StockLineChart() {
    return (
        <div>
            <h3 style={{ textAlign: 'center' }}>TITLE</h3>
            <Outter>
                <ResponsiveContainer width={`${list.length < 100 ? '100' : list.length * 1.5}%`} height='100%'>
                    <LineChart data={list} margin={{ top: 10, right: 20, left: 0, bottom: 10 }}>
                        <XAxis dataKey='date' />
                        <YAxis />
                        <CartesianGrid strokeDasharray='3 3' />
                        <rect x='60' y='150' width='100%' height='50' opacity='0.2' />;
                        <Tooltip content={<CustomTooltip />} />
                        <Legend />
                        <Line type='monotone' dataKey='high' stroke='red' />
                        <Line type='monotone' dataKey='preClose' strokeWidth='1.5' strokeDasharray='2' stroke='black' dot={false} />
                        <Line type='monotone' dataKey='low' stroke='blue' opacity='0.5' dot={false} />
                    </LineChart>
                </ResponsiveContainer>
            </Outter>
        </div>
    );
}
